@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);

#header {
  width: -webkit-max-content;
  width: max-content;
  padding: 5%;
  margin: 2% auto 0 auto;
  text-align: center;
  background-image: url(/memory-game/static/media/fruit.0ffd762b.png);
  background-position: center top;
  background-repeat: no-repeat;
  background-size: contain;
}

h1 {
  margin: 0;
  font-size: 3.5vw;
  letter-spacing: 0.1em;
}

h2 {
  margin: 0;
  font-size: 2vw;
  letter-spacing: 0.1em;
}

img {
  width: 100%;
  object-fit: contain;
}

#card_container {
  width: 60%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 8px;
  border-collapse: collapse;
}

.cards {
  padding: 5%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-content: center;
          align-content: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: pink;
}

#scoreboard {
    margin-right: 2%;
    background-color: rgb(182, 197, 96);
    height: -webkit-min-content;
    height: min-content;
    width: 20%;
    text-align: center;
    font-size: 2vw;
    border-radius: 10px;
}

#scoreboard > img {
    position: absolute;
    z-index: 9999;
    max-width: 20%;
    height: auto;
    left: 0;
    top: 20px;
}
body {
    margin: 0;
    background-color: rgb(253, 253, 253);
    background: url(/memory-game/static/media/background.12326ea7.jpg) right -50px bottom fixed no-repeat;
    background-size: contain;
    font-family: 'Staatliches', cursive;
  }

  #scoreboard_cards {
      margin: 0 2% 2% 6%;
      width: 72%;
      display: -webkit-flex;
      display: flex;
  }
